import { httpApi } from '@app/api/http.api';
import { AnswerModel } from '@app/domain/AnswerModel';
import { isTeacher } from '@app/services/localStorage.service';

export interface GeneralResponse {
  status: string;
  message: string;
}

export interface ExamTableRow {
  exam_id: string;
  exam_name: string;
  time_limit: number;
  published: boolean;
}

export interface QuestionChoices {
  no: string;
  question: string;
  choices: Record<string, string>;
}

export interface QuestionData {
  data: QuestionChoices[];
}

export interface Pagination {
  current?: number;
  pageSize?: number;
  total?: number;
}

export interface ExamTableData {
  data: ExamTableRow[];
  pagination: Pagination;
}

export interface CreateExamRequest {
  name: string;
  time_limit: number;
  question_file: string;
}

export const createExam = (createExamPayload: CreateExamRequest): Promise<GeneralResponse> =>
  httpApi.post<GeneralResponse>('/api/v1/exam/create', { ...createExamPayload }).then(({ data }) => data);

export const getQuestionData = (exam_id: string | undefined): Promise<QuestionData> =>
  httpApi.get<QuestionChoices[]>(`/api/v1/exam/${exam_id}/start`).then(({ data }) => {
    return new Promise((res) => {
      res({
        data,
      });
    });
  });

export const getBasicTableData = (pagination: Pagination): Promise<ExamTableData> =>
  isTeacher()
    ? httpApi.get<ExamTableRow[]>(`/api/v1/exam/all`).then(({ data }) => {
        return new Promise((res) => {
          res({
            data,
            pagination: { ...pagination, total: data.length },
          });
        });
      })
    : httpApi.get<ExamTableRow[]>(`/api/v1/exam/published`).then(({ data }) => {
        return new Promise((res) => {
          res({
            data,
            pagination: { ...pagination, total: data.length },
          });
        });
      });

export const submitAnswer = (exam_id: string | undefined, answers: AnswerModel): Promise<GeneralResponse> =>
  httpApi.post<GeneralResponse>(`/api/v1/exam/${exam_id}/submit`, { ...answers }).then(({ data }) => data);

export const publishExam = (exam_id: string): Promise<GeneralResponse> =>
  httpApi.put<GeneralResponse>(`/api/v1/exam/${exam_id}`).then(({ data }) => data);

export const deleteExam = (exam_id: string): Promise<GeneralResponse> =>
  httpApi.delete<GeneralResponse>(`/api/v1/exam/${exam_id}`).then(({ data }) => data);
