import React from 'react';
import { Col, Row } from 'antd';
import { ProfileDropdown } from '../components/profileDropdown/ProfileDropdown/ProfileDropdown';
import { SettingsDropdown } from '../components/settingsDropdown/SettingsDropdown';
import * as S from '../Header.styles';
import { Breadcrumb, BreadcrumbItem } from '@app/components/common/Breadcrumb/Breadcrumb';
import { useTranslation } from 'react-i18next';
import { useLocation, useNavigate } from 'react-router';
import { HomeOutlined } from '@ant-design/icons';
import { useParams } from 'react-router-dom';

interface DesktopHeaderProps {
  isTwoColumnsLayout: boolean;
}

export const DesktopHeader: React.FC<DesktopHeaderProps> = ({ isTwoColumnsLayout }) => {
  const { t } = useTranslation();
  const location = useLocation();
  const navigate = useNavigate();
  const params = useParams();
  const pathnames = location.pathname.split('/');
  const bcItem = pathnames.slice(1, pathnames.length).map((pathname) => {
    if (pathname == params.id) {
      return (
        <BreadcrumbItem key={pathname} href="#">
          {pathname}
        </BreadcrumbItem>
      );
    } else if (pathname && pathname != 'home') {
      return (
        <BreadcrumbItem key={pathname} onClick={() => navigate(pathname)} href="">
          {t('breadcrumbs.' + pathname)}
        </BreadcrumbItem>
      );
    } else {
      return (
        <BreadcrumbItem key={pathname} href={pathname}>
          Exam Platform
        </BreadcrumbItem>
      );
    }
  });

  const leftSide = isTwoColumnsLayout ? (
    <S.SearchColumn xl={16} xxl={17}>
      <Row justify="space-between">
        <Col xl={15} xxl={12}>
          <Breadcrumb>
            <BreadcrumbItem href="/">
              <HomeOutlined />
            </BreadcrumbItem>
            {bcItem}
          </Breadcrumb>
        </Col>
      </Row>
    </S.SearchColumn>
  ) : (
    <>
      <Col lg={10} xxl={8}>
        <Breadcrumb>
          <BreadcrumbItem href="/">
            <HomeOutlined />
          </BreadcrumbItem>
          {bcItem}
        </Breadcrumb>
      </Col>
    </>
  );

  return (
    <Row justify="space-between" align="middle">
      {leftSide}

      <S.ProfileColumn xl={8} xxl={7} $isTwoColumnsLayout={isTwoColumnsLayout}>
        <Row align="middle" justify="end" gutter={[10, 10]}>
          <Col>
            <Row gutter={[{ xxl: 10 }, { xxl: 10 }]}>
              <Col>
                <SettingsDropdown />
              </Col>
            </Row>
          </Col>

          <Col>
            <ProfileDropdown />
          </Col>
        </Row>
      </S.ProfileColumn>
    </Row>
  );
};
