import { createAction, createAsyncThunk, createSlice, PrepareAction } from '@reduxjs/toolkit';
import { AnswerModel } from '@app/domain/AnswerModel';
import { createExam, CreateExamRequest } from '@app/api/exam.api';
import { ScoreModel } from '@app/domain/ScoreModel';

const initialState: Record<string, unknown> = {};

export const setAnswer = createAction<PrepareAction<AnswerModel>>('answer/setAnswer', (newAnswer) => {
  return {
    payload: newAnswer,
  };
});

export const doCreate = createAsyncThunk('exam/doCreate', async (createExamPayload: CreateExamRequest) =>
  createExam(createExamPayload),
);

export const setScore = createAction<PrepareAction<ScoreModel>>('score/setScore', (score) => {
  return {
    payload: score,
  };
});

export const examSlice = createSlice({
  name: 'exam',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder.addCase(setAnswer, (state, action) => {
      state.answers = action.payload;
    });
    builder.addCase(setScore, (state, action) => {
      state.score = action.payload;
    });
  },
});

export default examSlice.reducer;
