import React from 'react';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';

// no lazy loading for auth pages to avoid flickering
const AuthLayout = React.lazy(() => import('@app/components/layouts/AuthLayout/AuthLayout'));
import LoginPage from '@app/pages/LoginPage';

import MainLayout from '@app/components/layouts/main/MainLayout/MainLayout';
import ProfileLayout from '@app/components/profile/ProfileLayout';
import RequireAuth from '@app/components/router/RequireAuth';
import { withLoading } from '@app/hocs/withLoading.hoc';

const ExamFormPage = React.lazy(() => import('@app/pages/CreateExamPage'));
const ListExamPage = React.lazy(() => import('@app/pages/ListExamPage'));
const StartExamPage = React.lazy(() => import('@app/pages/StartExamPage'));

const HomePage = React.lazy(() => import('@app/pages/HomePage'));
const ExamResultPage = React.lazy(() => import('@app/pages/ExamResultPage'));
const DataTablesPage = React.lazy(() => import('@app/pages/DataTablesPage'));
const ServerErrorPage = React.lazy(() => import('@app/pages/ServerErrorPage'));
const Error404Page = React.lazy(() => import('@app/pages/Error404Page'));
const PersonalInfoPage = React.lazy(() => import('@app/pages/PersonalInfoPage'));
const Logout = React.lazy(() => import('./Logout'));

export const NFT_DASHBOARD_PATH = '/';
export const MEDICAL_DASHBOARD_PATH = '/medical-dashboard';

const Home = withLoading(HomePage);
const ExamResult = withLoading(ExamResultPage);
const ExamForm = withLoading(ExamFormPage);
const ListExam = withLoading(ListExamPage);
const StartExam = withLoading(StartExamPage);

const DataTables = withLoading(DataTablesPage);

const ServerError = withLoading(ServerErrorPage);
const Error404 = withLoading(Error404Page);

// Profile
const PersonalInfo = withLoading(PersonalInfoPage);

const AuthLayoutFallback = withLoading(AuthLayout);
const LogoutFallback = withLoading(Logout);

export const AppRouter: React.FC = () => {
  const protectedLayout = (
    <RequireAuth>
      <MainLayout />
    </RequireAuth>
  );

  return (
    <BrowserRouter>
      <Routes>
        <Route path={NFT_DASHBOARD_PATH} element={protectedLayout}>
          <Route path="/" element={<Navigate replace to="/home" />} />
          <Route path="/home" element={<Home />} />
          <Route path="/exam/result" element={<ExamResult />} />
          <Route path="exam" element={<ListExam />} />
          <Route path="exam/:id/start" element={<StartExam />} />
          <Route path="exam">
            <Route path="create" element={<ExamForm />} />
            <Route path="list" element={<ListExam />} />
          </Route>
          <Route path="data-tables" element={<DataTables />} />
          <Route path="server-error" element={<ServerError />} />
          <Route path="*" element={<Error404 />} />
          <Route path="profile" element={<ProfileLayout />}>
            <Route path="personal-info" element={<PersonalInfo />} />
          </Route>
        </Route>
        <Route path="/auth" element={<AuthLayoutFallback />}>
          <Route path="login" element={<LoginPage />} />
        </Route>
        <Route path="/logout" element={<LogoutFallback />} />
      </Routes>
    </BrowserRouter>
  );
};
